.jonnitto-imagesinarow__row.jonnitto-imagesinarow__row:empty {
  cursor: pointer;
  justify-content: flex-end;
  align-items: center;
  margin: x 0;
  display: flex;
}

.jonnitto-imagesinarow__row.jonnitto-imagesinarow__row:empty::before, .jonnitto-imagesinarow__row.jonnitto-imagesinarow__row:empty::after {
  display: block;
  margin-right: 1em;
  opacity: 0.5;
  transition: opacity 0.2s ease;
}

.jonnitto-imagesinarow__row.jonnitto-imagesinarow__row:empty::before {
  content: attr(data-warning);
}

.jonnitto-imagesinarow__row.jonnitto-imagesinarow__row:empty::after {
  content: "";
  background: inline("img.svg") center no-repeat;
  size: 2em;
}

.jonnitto-imagesinarow__row.jonnitto-imagesinarow__row:empty:hover::before, .jonnitto-imagesinarow__row.jonnitto-imagesinarow__row:empty:hover::after {
  opacity: 1;
}

.jonnitto-imagesinarow__image {
  outline-offset: 0.1vw;
}

.jonnitto-imagesinarow__image[class*="style__markActiveNodeAsFocused"] {
  z-index: 1;
}

.jonnitto-imagesinarow__image[class*="style__markHoveredNodeAsHovered"] {
  z-index: 2;
}

@keyframes empty-jonnitto-imagesinarow {
  0%,
  25%,
  100% {
    transform: translateY(0);
  }
  5%,
  15% {
    transform: translateY(-4px);
  }
  10%,
  20% {
    transform: translateY(4px);
  }
}
